<template>
	<div class="card">
		<div class="flex-container">
			<h2>My plekjes</h2>
			<SegmentButton
				:labels="['Untagged', 'All']"
				v-on:pick="setListType"
                :picked="mode"
			></SegmentButton>
		</div>
		<div v-if="no_untagged && mode == 'Untagged'">
            <p>No untagged plekjes</p>
        </div>
		<div class="list" v-for="(p, j) in plekjesToDisplay">
			<div @click="selectPlekje(p, j)">
				<div v-if="j == selected_j" class="card">
					<div class="flex-container">
						<h3>{{ p.name }}</h3>
						<span>
							<button @click="tagging = !tagging" class="btn" v-bind:class="{tagging: tagging}">
								Tag
							</button>
							&nbsp;
							<button @click="map()" class="btn">Map</button>
						</span>
					</div>
					<div v-if="tagged_in.length > 0">
						<h4>Tagged in</h4>
						<div class="list" v-for="pc in tagged_in">
							<div @click="removeFrom(p, pc)" class="flex-container">
								{{ pc.name }}
								<button class="btn3" v-if="tagging">-</button>
							</div>
						</div>
					</div>
					<div v-if="tagging && not_tagged_in.length > 0">
						<h4>Add '{{ p.name }}' to</h4>
						<div class="list" v-for="(pc2, j) in not_tagged_in">
							<div @click="addTo(p, pc2)" class="flex-container">
								{{ pc2.name }}
                                <button class="btn3 btnplus" v-if="tagging">+</button>
							</div>
						</div>
					</div>
				</div>
				<div v-else>
					{{ p.name }}
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import SegmentButton from "./SegmentButton.vue";
var api = require("@/utils/api");
export default {
	components: {
		SegmentButton,
	},
	data() {
		return {
			selected_j: null,
			tagged_in: [],
			not_tagged_in: [],
			plekjesToDisplay: [],
			tagging: false,
			no_untagged: false,
			mode: "Untagged",
		};
	},
	methods: {
		selectPlekje(p, j) {
			if (this.selected_j != j) {
				this.tagging = false;
				this.tagged_in = [];
				this.selected_j = j;
				api.setSelectedPlekje(p._id, (pcs) => {
					this.tagged_in = pcs;
					this.not_tagged_in = [];
					let pc_names = pcs.map((x) => x.name);
					this.$store.state.userData.my_plekollections.forEach(
						(pc) => {
							if (!pc_names.includes(pc.name)) {
								this.not_tagged_in.push(pc);
							}
						}
					);
				});
			}
		},
		map() {
			api.addSinglePlekjeToMap();
		},
		removeFrom(p, pc) {
			if (this.tagging) {
				console.log(`removing ${p.name} from ${pc.name}`);
				this.tagged_in = this.tagged_in.filter((x) => x != pc);
				this.not_tagged_in.push(pc);
				api.updateSelectedPlekje(pc, "remove");
			}
		},
		addTo(p, pc) {
			// momenteel op naam -> doe op id zoda ge geen unieke naam nodig hebt
			console.log(`adding ${p.name} to ${pc.name}`);
			this.not_tagged_in = this.not_tagged_in.filter((x) => x != pc);
			this.tagged_in.push(pc);
			api.updateSelectedPlekje(pc, "add");
		},
		setListType(e,setup) {
			this.selected_j = null;
			this.tagging = false;
			if (e == "All") {
				this.mode = "All";
				this.plekjesToDisplay = this.$store.state.userData.my_plekjes;
			}
			if (e == "Untagged") {
				this.mode = "Untagged";
				// all plekjes that have no tags
				let ids = this.$store.state.userData.my_plekjes.map(
					(p) => p._id
				);
				api.getPlekjesWithoutTags(ids, (plekjes) => {
					this.plekjesToDisplay = plekjes;
					if (plekjes.length == 0) {
						this.no_untagged = true;
                        if(setup){
                            this.setListType("All");
                        }
					} else {
						this.no_untagged = false;
					}
				});
			}
		},
	},
	mounted() {
		this.setListType("Untagged",true);
	},
};
</script>

<style>

.btnplus{
    color:#28a745;
}

.tagging{
    color:#333;
}
</style>